import React from "react";
import {
  ContentDiv,
  FlexContainer,
  FlexItem,
  StyledButton,
} from "../globalStyles/GlobalStyles";
import PersonalDetails from "./PersonalDetails";
import Stacks from "./Stacks";
import { AboutMeContainer } from "./Styles";

const AboutMe = (props) => {
  return (
    <AboutMeContainer>
      {/* <AboutIcon /> */}

      <ContentDiv>
        <FlexContainer>
          <FlexItem>
            <PersonalDetails title="< PERSONAL DETAILS >" />
          </FlexItem>

          <FlexItem>
            <Stacks title="< STACKS && CODING SKILLS >" />
          </FlexItem>
        </FlexContainer>
      </ContentDiv>
      <StyledButton href="https://drive.google.com/file/d/1Ytz5mg5wum1eNM925MrYX4PbDZtrzX7I/view?usp=sharing">
        Download CV &nbsp;&nbsp;
        <span className="fas fa-download"></span>
      </StyledButton>
    </AboutMeContainer>
  );
};

export default AboutMe;
