import React, { useState } from "react";
import { useDispatch } from "react-redux";
import styled from "styled-components";
import { hideModal } from "../redux/ducks/globalStates";

const Modal = styled.div`
  display: block;
  position: fixed;
  z-index: 9;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(21, 25, 28, 0.522); /* Black background with opacity */
  backdrop-filter: blur(10px);
`;

const Close = styled.span`
  color: white;
  position: absolute;
  top: 10px;
  right: 25px;
  font-size: 35px;
  font-weight: bold;

  :hover,
  :focus {
    color: #999;
    text-decoration: none;
    cursor: pointer;
  }
`;

const ModalContent = styled.div`
  position: relative;
  margin: auto;
  padding: 0;
  width: 90%;

  max-width: 1200px;
`;

const ImageController = styled.a`
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 16px;
  margin-top: -35px;
  color: white;
  font-weight: bold;
  font-size: 50%;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
  -webkit-user-select: none;

  :hover {
    background-color: rgba(0, 0, 0, 0.8);
  }
`;

const NextBTN = styled(ImageController)`
  right: 0;
  border-radius: 3px 0 0 3px;
`;

const ProjectImage = styled.div`
  display: flex;
  justify-content: center;
  margin-top: -50px;
`;

const Imahe = styled.img`
  width: 80%;
  height: 20%;
`;

const PortfolioGallery = (props) => {
  const [count, setCount] = useState(0);
  const dispatch = useDispatch();

  const NextPicture = () => {
    setCount(count + 1);
  };

  const PrevPicture = () => {
    setCount(count - 1);
  };

  return (
    <Modal>
      <Close onClick={() => dispatch(hideModal())}>&times;</Close>
      <ModalContent>
        <ProjectImage>
          <Imahe
            src={`/images/${props.folder}/${props.folder + count}.png`}
            onError={() => setCount(0)}
          />
        </ProjectImage>

        <ImageController onClick={PrevPicture}>&#10094;</ImageController>
        <NextBTN onClick={NextPicture}>&#10095;</NextBTN>
      </ModalContent>
    </Modal>
  );
};
export default PortfolioGallery;
