import React from "react";
import { useSelector } from "react-redux";
import { ContentDiv, Headings } from "../globalStyles/GlobalStyles";
import { PortfolioCard } from "./Styles";

const Portfolio = (props) => {
  const StackObj = useSelector((state) => state.globalStates.StackObj);

  const PortfolioElements = [
    {
      id: 1,
      stackUsed: [StackObj[0], StackObj[1], StackObj[3], StackObj[6]],
      projectName: "SPDRRMO ONLINE INFORMATION SYSTEM",
      projectDescription:
        "A web application that aims to help the Sorsogon Provincial Disaster Risk Reduction and Management Office (SPDRRMO) gather necessary data and information about the overall status of each Municipality that have experience numerous types of disasters including minor and major accidents across the region. It also provides printable copies of all the information to make the reporting fast and easier. The web-app was built using React.JS in the frontend and PHP as its backend together with MySQL as its database.",
      projectFolder: "spdrrmo",
    },

    {
      id: 2,
      stackUsed: [StackObj[5], StackObj[8]],
      projectName: "DFBDSMH DOCUMENT CHECKLIST",
      projectDescription:
        "Is a Desktop Application that monitor the patients of Dr. Fernando B. Duran Sr. Memorial Hospital (DFBDSMH) who does/doesnâ€™t have submitted their PhilHealth requirements. The app also has the capabilities to provide printable reports of patients with complete and incomplete documents. It was built using the VB.NET and MySQL as its database.",
      projectFolder: "dfbdsmh",
    },

    {
      id: 3,
      stackUsed: [StackObj[5], StackObj[6]],
      projectName: "BSTV INVENTORY INFORMATION SYSTEM",
      projectDescription:
        "A Desktop Application that monitor and track the movement of items of Bulan Satellite Television. The app was built using VB.NET and MySQL as its database.",
      projectFolder: "bstv",
    },

    {
      id: 4,
      stackUsed: [StackObj[5], StackObj[7]],
      projectName: "LABELLA LUNA HOTEL MANAGEMENT SYSTEM",
      projectDescription:
        "A Desktop Application with basic hotel operations like room information and reservation, guest logging, income and expenses monitoring and yearly reservation monitoring. Developed using VB.NET and Firebird database.",
      projectFolder: "hotel",
    },

    {
      id: 5,
      stackUsed: [StackObj[5], StackObj[7]],
      projectName: "MILESTONE INVENTORY INFORMATION SYSTEM",
      projectDescription:
        "A Desktop Application that monitors and track the movement of items of Milestone Computer Parts and Servicing. The application also provides printable reports for business analyzation. The app was built using VB.NET and Firebird database.",
      projectFolder: "milestone",
    },

    {
      id: 6,
      stackUsed: [StackObj[5], StackObj[7]],
      projectName: "CWTS INFORMATION SYSTEM",
      projectDescription:
        "A Desktop Application that records all the student information enrolled in the Civic Welfare Training Service Program of the Sorsogon State College. The application also provides printable reports to be used by the facilitator. Developed using VB.NET and Firebird database.",
      projectFolder: "cwts",
    },
  ];

  return (
    <ContentDiv>
      <Headings>{props.title}</Headings>

      {PortfolioElements.map((elements) => {
        return <PortfolioCard key={elements.id} {...elements} />;
      })}
    </ContentDiv>
  );
};

export default Portfolio;
