const SHOW_MODAL = "showModal";
const HIDE_MODAL = "hideModal";
const SET_FOLDER = "setFolder";

export const showModal = () => ({
  type: SHOW_MODAL,
});

export const hideModal = () => ({
  type: HIDE_MODAL,
});

export const setFolder = (actfolder) => ({
  type: SET_FOLDER,
  actfolder,
});

