//reducers here
export default (state = initialState, action) => {
  switch (action.type) {
    case SHOW_MODAL:
      return { ...state, isModalActive: true };
    case HIDE_MODAL:
      return { ...state, isModalActive: false };
    case SET_FOLDER:
      const { actfolder } = action;
      return { ...state, actfolder };
    default:
      return { ...state };
  }
};
