import styled from "styled-components";

export const AboutMeContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  align-content: center;
  background-color: #1f2326;
  padding-top: 6rem;
  position: relative;
  -webkit-clip-path: polygon(0 10%, 100% 0, 100% 0%, 0 100%);
  clip-path: polygon(0 10%, 100% 0%, 100% 90%, 0 100%);
  z-index: 0;
  padding-bottom: 8rem;

  @media only screen and (max-width: 600px) {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 0%, 0 100%);
    clip-path: polygon(0 10%, 100% 0, 100% 95%, 0 100%);
  }
`;

export const List = styled.ul`
  color: ${(props) => props.textColor || "#e4e4e4"};
  font-size: 70%;
  font-family: "Montserrat", sans-serif;
  list-style-type: none;
  line-height: 2.8em;
  margin-left: -40px;
  margin-top: -25px;
  cursor: default;
`;

//Stacks Styles Below

export const StackDetails = styled.div`
  background-image: url(${(props) => props.url});
  margin-right: 20px;
  margin-bottom: 20px;
  background-color: rgba(21, 25, 28, 0.4); /* Black background with opacity */
  backdrop-filter: blur(10px);
  border: solid 2px transparent;
  background-clip: padding-box;
  background-position: 50% 30%;
  background-repeat: no-repeat;
  background-size: 30px, 30px;
  position: relative;
  width: 90px;
  height: 80px;
  border-radius: 4px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  transition: transform 0.2s;

  @media (max-width: 625px) {
    margin-bottom: 20px;
  }

  :hover {
    -ms-transform: scale(1.1); /* IE 9 */
    -webkit-transform: scale(1.1); /* Safari 3-8 */
    transform: scale(1.1);
  }
`;
