import React from "react";
import {
  StackList,
  StackText,
  SubHeadings,
} from "../globalStyles/GlobalStyles";
import { StackDetails, TableHead } from "./Styles";
import { useSelector } from "react-redux";

const Stacks = (props) => {
  const StackObj = useSelector((state) => state.globalStates.StackObj);
  return (
    <>
      <SubHeadings>{props.title}</SubHeadings>
      <StackList>
        {StackObj.map((stack) => {
          return (
            <StackDetails key={stack.id} url={stack.imageURL}>
              <StackText>{stack.language}</StackText>
            </StackDetails>
          );
        })}
      </StackList>
    </>
  );
};

export default Stacks;
