import styled from "styled-components";
import React from "react";

export const Headings = styled.p`
  font-family: NameFont;
  font-size: 100%;
  white-space: nowrap;
  letter-spacing: 0.3em;
  color: #c2a86f;
  font-weight: bold;
  text-align: center;
  margin-bottom: 50px;
  cursor: default;
  /* @media only screen and (max-width: 600px) {
    position: fixed;
    top: 3rem;
    font-size: 100%;
  } */
`;

export const SubHeadings = styled.p`
  text-decoration: none;
  font-family: NameFont;
  font-size: 0.8em;

  height: 2rem;
  white-space: nowrap;
  color: #c2a86f;
  text-align: left;
  cursor: default;
`;

export const ContentDiv = styled.div`
  display: block;
  flex-direction: column;
  width: 80%;
  align-items: center;
  position: relative;
  z-index: 0;
  margin: ${(props) => props.margin};

  animation: fadeIn ease 5s;
  -webkit-animation: fadeIn ease 5s;
  -moz-animation: fadeIn ease 5s;
  -o-animation: fadeIn ease 5s;
  -ms-animation: fadeIn ease 5s;

  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @-moz-keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @-webkit-keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @-o-keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @-ms-keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
`;

export const FullScreenDiv = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  @media only screen and (max-width: 600px) {
    height: 100vh;
  }
`;

export const ContentText = styled.p`
  font-family: "Montserrat", sans-serif;
  font-size: ${(props) => props.size || "80%"};
  color: ${(props) => props.textcolor || "#e8e8e8"};
  letter-spacing: 0.1em;
  line-height: 2em;
  text-align: ${(props) => props.align || "justify"};
  cursor: default;
`;

export const ColoredText = styled.span`
  font-family: "Montserrat", sans-serif;
  color: #fd9f5c;
  font-weight: bold;
  cursor: default;
`;

export const FlexContainer = styled.div`
  padding-top: 0.6em;
  display: flex;
  flex-direction: row;
  position: relative;
  align-items: left;
  width: 100%;
  font-family: "Montserrat", sans-serif;
  color: #e8e8e8;
  margin-top: 20px;

  @media (max-width: ${(props) => props.maxWidth || "1125px"}) {
    flex-direction: column;
  }
`;
export const FlexItem = styled.div`
  flex: ${(props) => props.flex || "50%"};
  position: relative;
  margin: ${(props) => props.margin || "0px 20px 20px 0px"};
`;

export const StackList = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;
  padding-right: 20px;
  position: relative;

  // @media (max-width: 625px) {
  //   flex-direction: column;
  // }
`;

export const StackText = styled(ContentText)`
  font-size: 10px;
  color: #a0a0a0;
  text-align: center;
  margin-top: 55px;
`;

export const StyledButton = styled.a`
  background-color: transparent;
  padding: 10px 0px 10px 0px;
  color: #a0a0a0;
  height: 20px;
  width: 150px;
  border: 2px solid #a0a0a0;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  font-weight: bold;
  z-index: 1;
  position: relative;
  transition: 0.3s;
  cursor: pointer;
  :hover {
    color: #bb994f;
    border: 2px solid #bb994f;
  }
`;

export const LinkInv = styled.a`
  text-decoration: none;
  position: relative;
  text-align: center;
  font-size: 25px;
  color: #acacac;
  margin: 0px 20px 0px 20px;

  :hover {
    color: #c2a86f;
  }
`;

const Icon = styled.img`
  width: 50px;
  height: 50px;
  transition: 0.3s;

  @media (max-width: 800px) {
    width: 30px;
    height: 30px;
  }

  :hover {
    filter: grayscale(100%);
  }
`;

export const SocialIco = (props) => {
  return (
    <LinkInv href={props.href}>
      <Icon src={props.src} />
    </LinkInv>
  );
};
