import React from "react";
import { List } from "./Styles";
import { useSelector } from "react-redux";
import {
  FlexContainer,
  FlexItem,
  SubHeadings,
} from "../globalStyles/GlobalStyles";

const PersonalDetails = (props) => {
  const personalInfo = useSelector(
    (state) => state.globalStates.personalInformation
  );

  return (
    <>
      <SubHeadings>{props.title}</SubHeadings>
      <FlexContainer maxWidth="0px">
        <FlexItem margin="0px" flex="20%">
          <List textColor="#fd9f5c">
            {personalInfo.map((info) => (
              <li key={info.id}>{info.detail}</li>
            ))}
          </List>
        </FlexItem>
        <FlexItem margin="0px">
          <List>
            {personalInfo.map((info) => (
              <li key={info.id}>{info.description}</li>
            ))}
          </List>
        </FlexItem>
      </FlexContainer>
    </>
  );
};

export default PersonalDetails;
