import { combineReducers, createStore, applyMiddleware } from "redux"; //redux core functions
import globalStatesReducer from "./ducks/globalStates"; //import the reducer

const reducer = combineReducers({
  //declare all the reducers you will be using
  globalStates: globalStatesReducer,
});

const store = createStore(reducer); //use the reducer and user the redux saga middleware

export default store;
