import React from "react";
import { useDispatch } from "react-redux";
import styled from "styled-components";
import { StackDetails } from "../Aboutme/Styles";
import {
  FlexItem,
  StackText,
  StyledButton,
} from "../globalStyles/GlobalStyles";
import { OverlayDark } from "../Header/Styles";
import { setFolder, showModal } from "../redux/ducks/globalStates";
import emoji from "emoji-node";

const CardContainer = styled.div`
  background-color: rgba(22, 26, 29, 0.2);
  position: relative;
  width: 100%;
  border-radius: 4px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  display: flex;
  flex-direction: row;
  position: relative;
  align-items: left;
  margin-bottom: 50px;
  z-index: 0;

  @media (max-width: 1125px) {
    flex-direction: column;
  }
`;

const Image = styled.div`
  background-image: url(${(props) => props.src});
  background-repeat: no-repeat;
  background-position: 50% 30%;
  background-size: cover;
  align-items: center;
  text-align: center;
  justify-content: center;
  display: flex;
  height: 450 px;
  flex: 40%;
  position: relative;
  padding: 50px 50px 50px 50px;
  flex-direction: column;
  z-index: 0;
`;

const GlassOverlay = styled(OverlayDark)`
  background-color: rgba(33, 33, 33, 0.6);
  backdrop-filter: blur(6px);
  z-index: 0;
  box-shadow: 5px 0 5px -5px #111111;
`;

const ProjectTitle = styled.p`
  z-index: 2;
  position: relative;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
  font-size: 0.8em;
  color: #c5c5c5;
  text-align: left;
  margin-left: 5%;
  font-weight: bold;
  cursor: default;
`;

const ProjectDesc = styled(ProjectTitle)`
  letter-spacing: 0.1em;
  line-height: 2em;
  font-weight: normal;
  font-size: 0.7em;
  cursor: default;
`;
const UsedStacks = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: row;
  position: relative;
  width: 100%;
  flex-wrap: wrap;
  padding-left: 20px;
`;

export const PortfolioCard = (props) => {
  const dispatch = useDispatch();

  const {
    stackUsed,
    projectName,
    projectDescription,
    projectFolder,
    id,
  } = props;

  const ShowModal = () => {
    dispatch(setFolder(projectFolder));
    dispatch(showModal());
  };

  return (
    <>
      <CardContainer>
        <Image src={id % 2 === 0 ? "/images/bg1.jpg" : "/images/coding.jpg"}>
          <GlassOverlay />

          <UsedStacks>
            {stackUsed.map((stackList) => {
              const { imageURL, language, id } = stackList;

              return (
                <StackDetails key={id} url={imageURL}>
                  <StackText>{language}</StackText>
                </StackDetails>
              );
            })}
          </UsedStacks>

          <StyledButton key={props.id} onClick={ShowModal}>
            VIEW IMAGE
          </StyledButton>
        </Image>
        <FlexItem>
          <ProjectTitle>
            {emoji.get("space_invader")}
            &nbsp;&nbsp;{projectName}
          </ProjectTitle>
          <ProjectDesc>{projectDescription}</ProjectDesc>
        </FlexItem>
      </CardContainer>
    </>
  );
};
