import styled from "styled-components";
import AutografFont from "../resources/fonts/Autograf.otf";

export const HeadContainer = styled.div`
  background-image: url("/images/header.jpg");
  background-repeat: no-repeat;
  background-position: 50% 30%;
  background-size: cover;
  display: flex;
  flex-direction: column;
  z-index: 1;
  width: 100%;
  height: 40vh;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 25px 0 rgba(0, 0, 0, 0.19);
  align-items: center;
  position: relative;

  @font-face {
    font-family: Autograf;
    src: url(${AutografFont});
  }

  @media only screen and (max-width: 1366px) {
    height: 270px;
  }
`;

export const OverlayDark = styled.div`
  position: absolute; /* Sit on top of the page content */
  display: block; /* Hidden by default */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(55, 55, 55, 0.2); /* Black background with opacity */
  backdrop-filter: blur(10px);

  background-clip: padding-box;
  z-index: 0; /* Specify a stack order in case you're using a different order for other elements */
`;

export const ProfileImage = styled.div`
  background-image: url("/images/profile.jpg");
  top: 28vh;
  position: absolute;
  background-size: cover;
  border: 5px solid #474646;
  border-radius: 50%;
  padding: 5px;
  width: 200px;
  height: 200px;

  @media only screen and (max-width: 1366px) {
    width: 150px;
    height: 150px;
    top: 65%;
  }
`;

export const YourName = styled.p`
  font-family: Autograf;
  font-size: 5em;
  margin-top: 10px;
  padding-bottom: 50px;
  white-space: nowrap;
  //letter-spacing: 0.3em;
  position: relative;
  background: -webkit-linear-gradient(left, #c69320, #dc1a5c);
  background: -o-linear-gradient(right, #c69320, #dc1a5c);
  background: -moz-linear-gradient(right, #c69320, #dc1a5c);
  background: linear-gradient(to right, #c69320 30%, #dc1a5c 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;

  //font-weight: bold;
`;
