import React from "react";
import { useSelector } from "react-redux";
import {
  ColoredText,
  ContentDiv,
  ContentText,
  FullScreenDiv,
  Headings,
  SocialIco,
} from "../globalStyles/GlobalStyles";
import PortfolioGallery from "../Portfolio/PortfolioGallery";
import { HeadContainer, OverlayDark, ProfileImage, YourName } from "./Styles";
import emoji from "emoji-node";

const Header = (props) => {
  const display = useSelector((state) => state.globalStates.isModalActive);
  const activeFolder = useSelector((state) => state.globalStates.actfolder);
  const { name, title } = props;
  return (
    <>
      {display && <PortfolioGallery folder={activeFolder} />}
      <FullScreenDiv id="top">
        <HeadContainer>
          <OverlayDark />
          <YourName>{name}</YourName>
          <ProfileImage />
        </HeadContainer>

        <ContentDiv margin="150px 0px 50px 0px">
          <Headings>{title}</Headings>

          <ContentText align="center">
            Hello {emoji.get("wave")} you can call me{" "}
            <ColoredText>Jeph</ColoredText>. Im a fullstack developer seeking an
            entry level career in the field of Software Development with a focus
            on Web Development.
          </ContentText>

          <ContentText align="center">
            <br />
            <SocialIco
              href="https://www.facebook.com/profile.php?id=100010018873886"
              src="/images/facebook.svg"
            ></SocialIco>
            <SocialIco
              href="https://github.com/jeph-kun"
              src="/images/github.svg"
            ></SocialIco>
            <SocialIco
              href="https://www.linkedin.com/in/jephunneh-viernes-571315168/"
              src="/images/linkedin.svg"
            ></SocialIco>
          </ContentText>
        </ContentDiv>
      </FullScreenDiv>
    </>
  );
};

export default Header;
