import {
  ContentDiv,
  ContentText,
  SocialIco,
} from "../globalStyles/GlobalStyles";
import { BacktoTop, FooterHR } from "./Styles";
import emoji from "emoji-node";

const Footer = () => {
  return (
    <ContentDiv margin="5vh 0vh 2vh 0vh">
      <BacktoTop />
      <ContentText align="center">
        <SocialIco
          href="https://www.facebook.com/profile.php?id=100010018873886"
          src="/images/facebook.svg"
        ></SocialIco>
        <SocialIco
          href="https://github.com/jeph-kun"
          src="/images/github.svg"
        ></SocialIco>
        <SocialIco
          href="https://www.linkedin.com/in/jephunneh-viernes-571315168/"
          src="/images/linkedin.svg"
        ></SocialIco>
      </ContentText>
      <FooterHR />
      <ContentText size="60%" align="center" textcolor="#9e9e9e">
        Made with {emoji.get("heart")} by Jephunneh Viernes
      </ContentText>
    </ContentDiv>
  );
};

export default Footer;
